body.page-template-contact {
  
  div.wpforms-container-full {
    margin: 0;

    //== Title
    .wpforms-title {
      font-size: inc_to_rem(4);
      font-weight: var(--font-bold);
      line-height: 1;
      margin-bottom: inc_to_rem(2);

      @include mq(desktop) {
        font-size: inc_to_rem(5);
      }

    }

    //== Description
    .wpforms-description {
      font-size: inc_to_rem(2);
      line-height: inc_to_rem(3);
      margin-bottom: inc_to_rem(5);
    }


    //== Error
    .wpforms-error-container {
      color: var(--form-error);
      font-size: inc_to_rem(2);
      margin-bottom: inc_to_rem(3);
      line-height: inc_to_rem(3);
        
      a {
        color: var(--form-error);
        text-decoration: underline;
      }
        
    }

    //== Form


    .wpforms-field-container {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -(inc_to_rem(1));

      .wpforms-field {
        flex-basis: 100%;
        margin-bottom: inc_to_rem(2);
        padding: 0 inc_to_rem(1);

        &:last-child { margin-bottom: 0; }

        input,
        select,
        textarea {
          border-radius: var(--border-radius);
          height: var(--form-input-height);
          font-size: inc_to_rem(2);
          max-width: none;
          padding: 0 inc_to_rem(2);

          &::-webkit-input-placeholder {
            color: var(--form-placeholder-colour);
          }

          &::-moz-placeholder {
            color: var(--form-placeholder-colour);
          }

          &:-ms-input-placeholder {
            color: var(--form-placeholder-colour);
          }

          &:-moz-placeholder {
            color: var(--form-placeholder-colour);
          }
          
        }

        textarea {
          height: var(--form-textarea-height);
          padding-top: inc_to_rem(2);
          padding-bottom: inc_to_rem(2);
        }


        //== Select

        &.wpforms-field-select {
          position: relative;

          &:after {
            border-style: solid;
            border-width: 0.25em 0.25em 0 0;
            border-color: black;
            content: '';
            display: block;
            height: 10px;
            position: absolute;
            right: inc_to_rem(3);
            top: 18px;
            transform: rotate(135deg);
            transition: transform .1s ease-in-out;
            vertical-align: top;
            width: 10px;
            z-index: 1;

            @include mq(tablet) {
              top: 22px;
            }

          }

          &--open {
            
            &:after {
              top: 23px;
              transform: rotate(315deg);

              @include mq(tablet) {
                top: 27px;
              }

            }

          }

          select {
            -webkit-appearance: none;
            -moz-appearance: none;
            text-indent: 1px;
            text-overflow: '';
          }

        }

        &.form-field--half {
          @include mq(tablet) {
            flex-basis: 50%;
          }
        }
        
        //== Error

        &.wpforms-has-error {

          input,
          select,
          textarea {
            background: var(--form-error);
            border: none;

            &::-webkit-input-placeholder {
              color: var(--form-placeholder-error-colour);
            }
  
            &::-moz-placeholder {
              color: var(--form-placeholder-error-colour);
            }
  
            &:-ms-input-placeholder {
              color: var(--form-placeholder-error-colour);
            }
  
            &:-moz-placeholder {
              color: var(--form-placeholder-error-colour);
            }
            
          }

          label.wpforms-error {
            background: none;
            color: var(--form-error);
            font-size: 16px;
            margin-top: inc_to_rem(1);
          }
          
        }

      }

    }

    .wpforms-submit-container {
      margin-top: inc_to_rem(3);

      button[type=submit] {
        background: none;
        @extend .btn;
        @extend .btn--secondary;
      }

    }


    //== Confirmation

    .wpforms-confirmation-container-full {
      background: none;
      border: none;
      margin: 0;
      padding: 0;

      p {
        color: var(--white);
        font-size: inc_to_rem(3);
        line-height: inc_to_rem(5);
      }

    }

  }

}